// Theme variables are globally available via CSS custom properties
.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background: linear-gradient(135deg, #e65100 0%, #ff6b35 50%, #ff8746 100%);
  
  &.light-theme {
    background: linear-gradient(135deg, #e65100 0%, #ff6b35 50%, #ff8746 100%);
  }
  
  &.dark-theme {
    background: linear-gradient(135deg, #00d4aa 0%, #00b894 100%);
  }
}

.login-card {
  width: 100%;
  max-width: 450px;
  background: white;
  border-radius: 24px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  padding: 48px;
  animation: slideUp 0.5s ease-out;

  .dark-theme & {
    background: #1e293b;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.login-header {
  text-align: center;
  margin-bottom: 32px;

  .login-icon {
    font-size: 64px;
    width: 64px;
    height: 64px;
    margin: 0 auto 16px;
    padding: 16px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .light-theme & .login-icon {
    background: linear-gradient(135deg, #ffedd5 0%, #fed7aa 100%);
    color: #f97316;
  }

  .dark-theme & .login-icon {
    background: linear-gradient(135deg, #334155 0%, #475569 100%);
    color: #10b981;
  }

  h1 {
    font-size: 32px;
    font-weight: 700;
    margin: 0 0 8px 0;
  }

  .light-theme & h1 {
    background: linear-gradient(135deg, #f97316 0%, #ea580c 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  .dark-theme & h1 {
    background: linear-gradient(135deg, #10b981 0%, #059669 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  .subtitle {
    font-size: 16px;
    margin: 0;
  }

  .light-theme & .subtitle {
    color: #78716c;
  }

  .dark-theme & .subtitle {
    color: #cbd5e1;
  }
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 20px;

  .error-message {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 12px 16px;
    border-radius: 12px;
    background: #fee2e2;
    color: #dc2626;
    font-size: 14px;
    animation: shake 0.3s ease-in-out;

    .dark-theme & {
      background: #7f1d1d;
      color: #fca5a5;
    }

    mat-icon {
      font-size: 20px;
      width: 20px;
      height: 20px;
    }
  }

  @keyframes shake {
    0%, 100% { transform: translateX(0); }
    25% { transform: translateX(-5px); }
    75% { transform: translateX(5px); }
  }

  .full-width {
    width: 100%;
  }

  .login-button {
    width: 100%;
    height: 52px;
    font-size: 16px;
    font-weight: 600;
    border-radius: 12px;
    margin-top: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    transition: all 0.3s ease;

    .light-theme & {
      background: linear-gradient(135deg, #f97316 0%, #ea580c 100%);
      color: white;

      &:hover:not(:disabled) {
        transform: translateY(-2px);
        box-shadow: 0 8px 20px rgba(249, 115, 22, 0.4);
      }
    }

    .dark-theme & {
      background: linear-gradient(135deg, #10b981 0%, #059669 100%);
      color: white;

      &:hover:not(:disabled) {
        transform: translateY(-2px);
        box-shadow: 0 8px 20px rgba(16, 185, 129, 0.4);
      }
    }

    &:disabled {
      opacity: 0.6;
      cursor: not-allowed;
    }

    mat-spinner {
      display: inline-block;
      margin-right: 8px;

      ::ng-deep circle {
        stroke: white;
      }
    }

    mat-icon {
      font-size: 24px;
      width: 24px;
      height: 24px;
    }
  }

  .login-hint {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    padding: 12px;
    border-radius: 12px;
    font-size: 13px;
    margin-top: 8px;
  }

  .light-theme & .login-hint {
    background: linear-gradient(135deg, #fff8f0 0%, #ffedd5 100%);
    color: #78716c;
    border: 1px solid #fed7aa;

    mat-icon {
      color: #f97316;
    }
  }

  .dark-theme & .login-hint {
    background: linear-gradient(135deg, #1e293b 0%, #334155 100%);
    color: #cbd5e1;
    border: 1px solid #475569;

    mat-icon {
      color: #10b981;
    }
  }

  .login-hint mat-icon {
    font-size: 18px;
    width: 18px;
    height: 18px;
  }
}

// 반응형
@media (max-width: 768px) {
  .login-card {
    padding: 32px 24px;
  }

  .login-header {
    .login-icon {
      font-size: 56px;
      width: 56px;
      height: 56px;
    }

    h1 {
      font-size: 28px;
    }
  }
}
